import utils from '@/components/evan-form/utils.js';
import RepairFn from '../../api/repair/index.js';
import tool from '../../utils/index.js'
import uniIcons from "@/components/uni-icons/uni-icons.vue"
export default {
	/*components: {
		uniIcons
	},*/
	data() {
		return {
			typelist:[],
			typenames:[],
			typeindex:0,
			repairform: {
				status:'01',
				type:'',
				'repairno':'',
				'title':'',
				'substance':'',
				'adduser':'',
				'addusertel':'',
				'note':'',
				'images':[]
			},
			rules: {
				repairno: {
					required: true,
					message: '请输入报修单号'
				},
				type: [{
					required: true,
					message: '请选择类型'
				}],
				title: [{
					required: true,
					message: '请输入标题'
				}],
				substance: [{
					required: true,
					message: '请描述问题'
				}]
			}
		}
	},
	mounted() {
		this.$refs.form.setRules(this.rules);
		this.gettypes();
		this.getrepairno();
		this.init();
	},
	methods: {
		gettypes(){
			RepairFn.repairtypes().then(result=>{
				if(result.data.code ===1){
					this.typelist = result.data.result;
					this.typenames = result.data.result.map(function(i){
						return i.name;
					});
					this.repairform.type = this.typelist[0].code;
				}
			})
		},
		getrepairno(){
			RepairFn.repairno().then(result=>{
				this.repairform.repairno = result.data.result;
			});
		},
		init(){
			try{
				const userinfo = uni.getStorageSync('userinfo');
				this.repairform.adduser = userinfo.name;
				this.repairform.addusertel = userinfo.tel;
			}
			catch(e){
				
			}
		},
		selecttype(e) {
			console.log(e.target.value);
			this.typeindex = e.target.value;
			this.repairform.type = this.typelist[this.typeindex].code;
		},
		chooseimg(imgtype){
			const that = this;
			uni.chooseImage({
			    count: 9, //默认9
			    sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: [imgtype], //从相册选择
			    success: function (res) {
			        console.log(JSON.stringify(res.tempFilePaths));
					const tempFilePaths = res.tempFilePaths;
					//删除文件
					RepairFn.removeimgs({
						"images":that.repairform.images
					}).then(response=>{
						that.repairform.images=[];
						//上传文件
						tempFilePaths.forEach(function(item){
							uni.showLoading({
								title:"文件上传中……"
							});
							uni.uploadFile({
							    url: RepairFn.repairimg_uploadurl,
								 header:tool.getheader(),
							    filePath: item,
							    name: 'file',
							    success: (uploadFileRes) => {
									let resdata = JSON.parse(uploadFileRes.data);
									uni.showToast({
										title:resdata.msg,
										duration:2000
									});
									that.repairform.images.push(resdata.result);
							    }
							});
						});
					});   
			    }
			});
		},
		opensheet(){
			const that = this;
			uni.showActionSheet({
			    itemList: ['相册', '照相机'],
			    success: function (res) {
			        let index = res.tapIndex;
					switch (index){
						case 0:
							that.chooseimg('album');
							break;
						case 1:
							that.chooseimg('camera');
							break;
						default:
							break;
					}
			    },
			    fail: function (res) {
			        console.log(res.errMsg);
			    }
			});
			
			
		},
		submit_form(){
			this.$refs.form.validate((res) => {
				if (res) {
					RepairFn.addrepair(this.repairform).then(res=>{
						let result = res;
						uni.showToast({
							title:result.data.msg,
							duration:3000,
						});
						if(result.data.code === 1){
							uni.reLaunch({
								url:'../my/index'
							})
						}
					});
				}
			});
		}
	}
}
